export interface IdpResponseData {
    idpAlias: string;
    displayName: string;
    enabled: string;
    providerId: string;
    createdDate: string;
    lastUpdatedDate: string;
    lastUpdatedBy: string;
    error?: string;
}

export const IdpResponseDataInitialState =
    {idpAlias: '', displayName: '', enabled: '', providerId: '', createdDate: '', lastUpdatedDate: '', lastUpdatedBy: '' }

export interface SamlResponseData extends IdpResponseData {
    identityProviderEntityId: string;
    authenticationRequestsUrl: string;
    x509Certificate: string;
    serviceProviderEntityId: string;
}

export interface OidcResponseData extends IdpResponseData {
    clientId: string;
    clientSecret: string;
    authorizationUrl: string;
    tokenUrl: string;
    userInfoUrl: string;
    issuerUrl: string;
    jwksUrl: string;
}
