import React from 'react';
import {SimpleText} from "../../Components/AuditInfo/formatters/simple-text";
import './audit-info.scss'

export interface AuditTypeFormatter {
    // eslint-disable-next-line no-unused-vars
    format(value: string): string;
}

type AuditInfoProps = {
    preValue?: string;
    value: string;
    postValue?: string;
    formatter?: AuditTypeFormatter;
};

const AuditInfo: React.FC<AuditInfoProps> = ({ preValue, value, postValue, formatter = new SimpleText() }) => {
    return <div className={'audit-info'}>{preValue} {formatter.format(value)} {postValue} </div>;
};

export default AuditInfo;
