import {AuditTypeFormatter} from '../../../Components/AuditInfo/audit-info';

export class SimpleText implements AuditTypeFormatter {
    readonly BLANK_VALUE = '';

    // eslint-disable-next-line class-methods-use-this
    format(value: string): string {
        if (value.trim().length > 0) {
            return value;
        }
        return this.BLANK_VALUE;
    }
}
