import {AuditTypeFormatter} from "../../../Components/AuditInfo/audit-info";

export class AuditDate implements AuditTypeFormatter {
    readonly BLANK_VALUE = '';

    // eslint-disable-next-line class-methods-use-this
    format(value: string): string {
        try {
            const date = new Date(value.replace('+0000', 'Z'));

            if (isNaN(date.getTime())) { return this.BLANK_VALUE; }

            return date.toLocaleString('en-US', {
                year: 'numeric',
                month: 'short',
                day: 'numeric',
                hour: 'numeric',
                minute: 'numeric',
                hour12: true,
                timeZone: 'UTC',
            });
        } catch (error) {
            return this.BLANK_VALUE;
        }
    }
}
